@mixin telefono {
    @media (min-width: $telefono){
        @content;
    }
}

@mixin tablet {
    @media (min-width: $tablet){
        @content;
    }
}

@mixin desktop {
    @media (min-width: $desktop){
        @content;
    }
}
@mixin grid($column, $espacio){
    display: grid;
    grid-template-columns: repeat($column, 1fr);
    gap: $espacio;
}